body {
  font-family: "Raleway-Regular";
  text-rendering: geometricPrecision;
  color: #333333;
  line-height: 1.6em;
}

.centered, .center {
  text-align: center;
}

.button {
  display: block;
  padding: 15px 5px;
  width: 100%;
  max-width: 300px;
  border-radius: 62px;
}

a, button {
  color: inherit;
  text-decoration: inherit;
  border: 0;
}

button {
  cursor: pointer;
  max-width: 300px;
  width: 100%;
  padding: 15px 25px;
  border-radius: 100px;
  display: block;
}
button.green {
  background: #D1FD38;
  color: #0088CC;
  font-size: 20px;
}
button.blue {
  background: #0088CC;
  color: #D1FD38;
  font-size: 20px;
}
button.centered {
  margin: 0 auto;
}

#whatsapp {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}

svg {
  max-width: 100%;
}

.intro {
  width: 100vw;
  max-width: 100%;
  height: 100vh;
  max-height: 850px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.intro > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  object-position: center;
  position: absolute;
}
.intro .main-title {
  position: relative;
  display: block;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  color: #fff;
  padding: 0 35px;
  min-height: 500px;
  max-height: 400px;
  font-size: 20px;
}
.intro .main-title img {
  width: 100%;
  max-width: 600px;
}
@media (max-width: 724px) {
  .intro .main-title {
    padding: 0 15px;
    align-self: unset;
  }
}

.descanso {
  padding: 100px 0;
}
.descanso div {
  padding: 80px 0;
  max-width: 1024px;
  margin: 0 auto;
  font-size: 25px;
  border-top: 1px dotted rgba(187, 189, 191, 0.5);
  border-bottom: 1px dotted rgba(187, 189, 191, 0.5);
  line-height: 1.5em;
}
.descanso div p {
  padding: 0 35px;
}
.descanso.no-border div {
  border: 0;
}
.descanso.no-border div p {
  padding: 0;
  line-height: 1.8em;
  font-size: 22px;
}

.fullWidthImage {
  width: 100%;
  max-height: 800px;
  height: 80vh;
}

#img-1 {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}

.descanso-2 {
  background: #D1FD38;
  color: #0088CC;
  font-size: 22px;
  padding: 80px 35px;
}
.descanso-2 div {
  max-width: 1024px;
  margin: 0 auto;
  line-height: 1.5em;
}
.descanso-2 div img {
  margin-left: -15px;
}
.descanso-2 div p {
  padding-top: 30px;
}

.siete-pilares {
  background: #0088CC;
}
.siete-pilares .info {
  max-width: 1024px;
  margin: 0 auto;
}
.siete-pilares .info .titulo {
  display: flex;
  align-items: flex-end;
}
.siete-pilares .info .titulo #excelencia {
  margin-left: 35px;
}
.siete-pilares .info .pilares {
  padding: 100px 0;
  color: #fff;
  font-size: 20px;
  text-align: center;
  width: 100%;
}
@media (min-width: 1024px) {
  .siete-pilares .info .pilares {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 4em 2rem;
  }
}
@media (max-width: 1023px) {
  .siete-pilares .info .pilares {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 4em 2rem;
  }
}
.siete-pilares .info .pilares .pilar .icon {
  width: 135px;
  height: 135px;
  background: #fff;
  border-radius: 150px;
  margin: 0 auto 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.siete-pilares .info .pilares .pilar .icon .image svg path {
  fill: #0088CC;
}
.siete-pilares #pilar-5 svg path {
  fill: #fff;
}
.siete-pilares #pilar-5 svg path:nth-child(1) {
  fill: #0088CC;
}

.soluciones {
  width: 100%;
  max-height: 700px;
  height: 100vh;
  background-color: #0088CC;
  display: flex;
  align-items: center;
}
.soluciones .info {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 0 auto;
  flex-direction: column;
  height: 100%;
  min-height: 400px;
  max-height: 60%;
}
.soluciones .title {
  font-size: 59px;
  line-height: 1em;
  color: #fff;
}
.soluciones .icon svg path {
  fill: #D1FD38;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

.residuos {
  background: #F4F4F4;
  padding: 80px 35px;
}
.residuos .title {
  color: #0088CC;
  padding-bottom: 7rem;
  font-size: 3rem;
}
.residuos .residuos-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  grid-gap: 6em 4rem;
}
.residuos .residuos-list .icon img {
  max-width: 100%;
}
.residuos .residuos-list .name {
  padding-top: 15px;
  font-size: 1.8rem;
}

.contacto {
  background: #D1FD38;
  padding: 100px 15px;
}
.contacto .datos {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  grid-gap: 6em 4rem;
  font-size: 16px;
}
.contacto .datos .title {
  font-size: 20px;
  margin-bottom: 2rem;
}
.contacto .datos .icon {
  margin-bottom: 2rem;
}
.contacto .form form {
  max-width: 760px;
  margin: 80px auto 0;
}
.contacto .form form .input {
  margin-bottom: 1rem;
  display: block;
}
.contacto .form form .input input, .contacto .form form .input textarea {
  display: block;
  width: 100%;
  padding: 7px 15px;
  color: #333333;
  box-sizing: border-box;
}
.contacto .form form .input textarea {
  min-height: 150px;
  margin-bottom: 3rem;
  resize: none;
}